import { json } from "@remix-run/node";
import { useLoaderData } from "@remix-run/react";
import Title from "../components/Title";
import { client } from "../models/contentful.server";
import { motion } from 'framer-motion';
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';
import { BLOCKS, INLINES } from '@contentful/rich-text-types';

export async function loader() {
	const becomeAsponsor = await client.getBecomeAsponsor();
	const page = await client.getPage("Become A Sponsor")
	return json({becomeAsponsor, page});
}

export const meta= ({data}) =>{
	const { title } = data.page.seoMetadata
	return {
		title

	}
}

export const richTextRenderOptions = {
	renderNode: {
		[INLINES.HYPERLINK]: (node, children) => {
			const { data } = node;
			const { uri } = data;
			return (
					<a
						className="text-primary underline dark:text-secondary"
						target="_blank"
                        href={uri}
					>
						{children[0]}
					</a>
			);
		},
		[BLOCKS.PARAGRAPH]: (node, children) => {
			return (
				<p className="text-gray-700 text-base dark:text-gray-400">{children}</p>
			);
		},
	},
};

export default function becomeAsponsor() {
    const {becomeAsponsor} = useLoaderData()
    return (
        <div className="px-4 sm:px-48">
				<Title title="Projects" emoji="💻" />
				<div className="grid gap-4 sm:grid-cols-3">
					{projects.map((project) => {
						return (
							<motion.div
								key={project.title}
								className="mt-8 max-w-sm rounded overflow-hidden shadow-lg bg-white h-auto dark:bg-gray-700"
								whileHover={{
									scale: 1.02,
									transition: {
										duration: 0.2,
										ease: 'easeInOut',
									},
								}}
							>
								<div
									style={{
										position: 'relative',
										display: 'block',
										overflow: 'hidden',
									}}
								>
									<div
										style={{
											position: 'absolute',
											top: 0,
											right: 0,
											bottom: 0,
											left: 0,
											width: '100%',
											height: '100%',
											transform: 'scale(1.5)',
											filter: 'blur(40px)',
											// ...project.css,
										}}
									/>
									<img {...becomeAsponsor.image} />
								</div>
								<div className="px-6 py-4">
									<h2 className="font-bold text-lg sm:text-xl mb-2 dark:text-secondary">
										{project.title}{' '}
										<a href={becomeAsponsor.link} target="_blank">
											<span
												role="image"
												aria-label="internet"
												className="cursor-pointer"
											>
												🔗
											</span>
										</a>
									</h2>
									{documentToReactComponents(
										becomeAsponsor.desc.json,
										richTextRenderOptions
									)}
								</div>
							</motion.div>
						);
					})}
				</div>
			</div>
    )
}